@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
body {
  margin: 0;
  font-family: 'Lato', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,*::before,*::after{box-sizing:border-box}h1,h2{margin:0}ul{list-style:none;padding:0;margin:0}.recipes{display:flex;justify-content:space-around;flex-wrap:wrap;max-width:1200px;margin:auto;padding:36px 0}

.alert h4{color:#ff0000;text-align:center}

header{background-color:#82c836;padding:25px 0}header h1{font-family:'Major Mono Display', monospace;text-align:center;color:white;font-size:1rem}@media (min-width: 768px){header h1{font-size:2rem}}

footer{background-color:#82c836;padding:15px 0}footer p{font-size:.9em;text-align:center;color:white}footer .logo{text-align:center}

.recipe-card{border-radius:10px;box-shadow:0px 5px 15px #626262;margin:10px;padding:30px;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.recipe-card h2{font-size:1.3rem;text-align:center}@media (min-width: 768px){.recipe-card h2{font-size:1.5rem}}.recipe-card h4{margin-bottom:5px}.recipe-card li{font-size:.8em;font-style:italic;list-style:square}

.search-form{margin:30px;text-align:center}.search-form .search-bar{padding:5px;width:60%}@media (min-width: 768px){.search-form .search-bar{width:30%}}.search-form .search-btn{font-family:'Lato', sans-serif;padding:5px 15px;margin-left:10px;cursor:pointer;border-radius:5px;background-color:#82c836;color:#fff;border:none;font-weight:bold;outline:none}.search-form .search-btn:hover{background-color:#8ddf37}

